import React, { useState } from "react";
import "./sidebar.scss";
const TYPE = {
  FOLDER: "FOLDER",
  FILE: "FILE"
};

export default () => {
  const folders = [
    {
      id: "movies",
      type: TYPE.FOLDER,
      title: "Movies",
      folders: [
        {
          id: "gameofthrones",
          type: TYPE.FOLDER,
          title: "Game of Thrones",
          folders: [
            {
              id: "season01",
              type: TYPE.FOLDER,
              title: "Season 01",
              folders: [
                {
                  id: "episode01",
                  type: TYPE.FOLDER,
                  title: "Episode 01"
                },
                {
                  id: "episode02",
                  type: TYPE.FOLDER,
                  title: "Episode 02"
                },
                {
                  id: "episode03",
                  type: TYPE.FOLDER,
                  title: "Eeason 03"
                }
              ]
            },
            {
              id: "season02",
              type: TYPE.FOLDER,
              title: "Season 02"
            },
            {
              id: "season03",
              type: TYPE.FOLDER,
              title: "Season 03"
            }
          ]
        },
        {
          id: "vikings",
          type: TYPE.FOLDER,
          title: "Vikings"
        },
        {
          id: "thedarkKnightRises",
          type: TYPE.FOLDER,
          title: "Dark Knight Rises"
        }
      ]
    }
  ];

  const renderFolder = (folder = {}, depth = 0) => {
    const subFolders = folder.folders;
    const [expand, setExpand] = useState(false);
    return (
      <div key={folder.id} style={{paddingLeft: `${depth*12}px`}}>
        <div className="folder" onClick={() => setExpand(true)}>
          {!expand ? <i className="fas fa-folder" /> : <i className="fas fa-folder-open" /> }
          <span className="title">{folder.title}</span>
        </div>
        {subFolders && expand && subFolders.map(folder => renderFolder(folder, depth + 1))}
      </div>
    );
  };
  return (
    <div className="folder-structure">
      <div className="sidebar">{folders.map(renderFolder)}</div>
      <div className="content"></div>
    </div>
  );
};
